import {
  Collapse,
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Theme,
  createStyles
} from '@mui/material'
import {
  ColorLensTwoTone,
  ExpandLess,
  ExpandMore,
  FiberNewRounded,
  LibraryAddRounded,
  ShuffleRounded,
  ViewListRounded
} from '@mui/icons-material'
import * as React from 'react'

import { getTheme } from '../theme'
import { baseProps } from '../interfaces/stateInterfaces'
import { Themes } from './Themes'

const styles = (theme: Theme) =>
  createStyles({
    list: {
      overflowY: 'scroll',
      position: 'relative',
      width: '100%'
    }
  })

export const Sidebar: React.FC<baseProps> = ({ store, dispatch }) => {
  const fnOnClick = () => {
    window.open('https://boomza.com', '_system')
  }
  const currentTheme = getTheme(store.themeIndex)
  const [isOpen, setOpen] = React.useState(false)

  return (
    <Drawer
      anchor="right"
      open={store.sidebarActive}
      onClick={() => {
        dispatch({ type: 'SHOW_SIDEBAR', value: false, index: null })
      }}
    >
      <List>
        <ListItem disablePadding>
          <ListItemButton dense={true} onClick={fnOnClick} disabled={true}>
            <ListItemIcon>
              <ShuffleRounded />
            </ListItemIcon>
            <ListItemText primary="Shuffle On" />
          </ListItemButton>
        </ListItem>
        <Divider />
        <ListItem disablePadding>
          <ListItemButton
            dense={true}
            onClick={(e) => {
              e.stopPropagation()
              setOpen(!isOpen)
            }}
          >
            <ListItemIcon>
              <ColorLensTwoTone />
            </ListItemIcon>
            <ListItemText primary="Themes" />
            {isOpen ? <ExpandLess /> : <ExpandMore />}
          </ListItemButton>
        </ListItem>
        <Collapse
          in={isOpen}
          timeout="auto"
          unmountOnExit
          sx={styles(currentTheme)['list']}
        >
          <Themes store={store} dispatch={dispatch} /> 
        </Collapse>
      </List>
    </Drawer>
  )
}
