import {
  Button,
  Card,
  CardActions,
  CardContent,
  Theme,
  Typography,
  createStyles
} from '@mui/material/'
import * as React from 'react'
import { baseProps } from '../interfaces/stateInterfaces'
import { getTheme } from '../theme'

const styles = (theme: Theme) =>
  createStyles({
    Card: {
      marginBottom: 'auto',
      marginRight: 'auto',
      marginLeft: 'auto',      
      marginTop: '10%',
      maxWidth: 345
    },
    CardActions: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
      justifyContent: 'center'
    }
  })

export const Notice: React.FC<baseProps> = ({ store, dispatch }) => {
  const fnOnClick = () => {
    window.open('https://boomza.com', '_system')
  }

  const currentTheme = getTheme(store.themeIndex)
  return (
    <Card raised sx={styles(currentTheme)['Card']}>
      <CardContent>
        <Typography component="p">
          Your current deck is empty. Please use button "CREATE NEW DECK" to
          create new one. You can always add or remove words from deck using
          side menu.
        </Typography>
        <Typography component="p">
          For additional help use &nbsp;
          <a href="#" onClick={fnOnClick}>
            Boomza.com
          </a>
          .
        </Typography>
      </CardContent>
      <CardActions sx={styles(currentTheme)['CardActions']}>
        <Button size="small" color="inherit" onClick={fnOnClick}>
          CREATE NEW DECK
        </Button>
      </CardActions>
    </Card>
  )
}
