import { ThemeProvider } from '@mui/material'
import CssBaseline from '@mui/material/CssBaseline'
import * as React from 'react'
import { Cards } from './Cards'
import { Home } from './Home'
import { Mess } from './Mess'
import { Profile } from './Profile'
import { Footer } from './components/Footer'
import { Header } from './components/Header'
import { Sidebar } from './components/Sidebar'
import reducer from './reducer'
import initialState from './reducer/initialState'
import { getTheme } from './theme'

export function MainLayout() {
  const [store, dispatch] = React.useReducer(reducer, initialState)
  React.useEffect(() => {
    const _temp = localStorage.getItem('boomzaState')
    if (_temp !== null)
      dispatch({
        type: 'INIT',
        value: JSON.parse(_temp),
        index: 0
      })
  }, [])

  React.useEffect(() => {
    localStorage.setItem('boomzaState', JSON.stringify(store))
  }, [store])

  return (
    <ThemeProvider theme={getTheme(store.themeIndex)}>
      <CssBaseline enableColorScheme />
      <Header store={store} dispatch={dispatch} />
      <Sidebar store={store} dispatch={dispatch} />
      {store.pageIndex === 0 && <Home store={store} dispatch={dispatch} />}
      {store.pageIndex === 1 && <Mess store={store} dispatch={dispatch} />}
      {store.pageIndex === 2 && <Cards store={store} dispatch={dispatch} />}
      {store.pageIndex === 3 && <Profile store={store} dispatch={dispatch} />}

      <Footer store={store} dispatch={dispatch} />
    </ThemeProvider>
  )
}
