import { actionType, appStateType } from '../interfaces/stateInterfaces'
import { getTheme } from '../theme'

export default function reducer (state: any, action: actionType): appStateType {
  switch (action.type) {
    case 'INIT':
      return {
        ...action.value
      }
    case 'SELECT_FROM':
      return {
        ...state,
        selectedFrom: action.value
      }
    case 'SELECT_TO':
      state.selectedTo[action.index] = action.value
      return {
        ...state
      }
    case 'RESET':
      return {
        ...state,
        selectedFrom: null,
        selectedTo: [false, false, false, false]
      }
    case 'THEME_CHANGE':
      return {
        ...state,
        themeIndex: action.index
      }    
      case 'PAGE_CHANGE':
      return {
        ...state,
        pageIndex: action.index
      }
      case 'SHOW_SIDEBAR':
        return {
          ...state,
          sidebarActive: action.value
        }
    default:
      return state
  }
}
