import { Button, Color, List, ListItem, Theme, createStyles } from '@mui/material/'
import * as React from 'react'
import { baseProps } from '../interfaces/stateInterfaces'
import { getTheme, availableColors } from '../theme'
import { Colorize } from '@mui/icons-material'
import reducer from '../reducer'
import initialState from '../reducer/initialState'
const allColorsList = () => {
  const colorList: {[key: string]:{}} = {}
  availableColors.forEach((color: Color, index: number) => {
    // tslint:disable-next-line:no-string-literal
    colorList['color-' + index] = {
      backgroundColor: color[900],
      color: color[50]      
    }
  })
  return colorList
}

const styles = (theme: Theme) =>
  createStyles({
    ...allColorsList(),
    button: {
      margin: '1em'
    },
    extendedIcon: {
      marginRight: '1em'
    },
    list: {
      position: 'relative'
    },
    nested: {
      justifyContent: 'center',
      paddingLeft: 'auto'
    }
  })

export const Themes: React.FC<baseProps>  = ({ store, dispatch }) => {
  //const [store, dispatch] = React.useReducer(reducer, initialState);
  const currentTheme = getTheme(store.themeIndex)
  const onThemeChange = (index: number) => {
    dispatch({ type: 'THEME_CHANGE', value: index, index: index });
  }

  return (
    <List component="div" disablePadding className={styles(currentTheme)['list']}>
      {availableColors.map((css: any, index: number) => (
        <ListItem key={index.toString()} sx={styles(currentTheme)['nested']}>
          <Button
            onClick={() => onThemeChange(index)}            
            sx={styles(currentTheme)['color-' + index]}            
          >
            <Colorize />
          </Button>
        </ListItem>
      ))}
    </List>
  )
}
