import { BottomNavigation, BottomNavigationAction, Paper } from '@mui/material/'
import PersonOutlinedIcon from '@mui/icons-material/PersonOutlined'
import StyleOutlinedIcon from '@mui/icons-material/StyleOutlined'
import HomeIcon from '@mui/icons-material/Home'
import VocabularyIcon from '@mui/icons-material/LibraryBooksOutlined'

import { baseProps } from '../interfaces/stateInterfaces'

import * as React from 'react'

export const Footer: React.FC<baseProps> = ({ store, dispatch }) => {
  return (
    <Paper
      sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }}
      elevation={3}
    >
      <BottomNavigation
        showLabels
        value={store.pageIndex}
        onChange={(event, newValue) => {
          dispatch({ type: 'PAGE_CHANGE', value: newValue, index: newValue })
        }}
      >
        <BottomNavigationAction icon={<HomeIcon />} />
        <BottomNavigationAction icon={<StyleOutlinedIcon />} />
        <BottomNavigationAction icon={<VocabularyIcon />} />
        <BottomNavigationAction icon={<PersonOutlinedIcon />} />
      </BottomNavigation>
    </Paper>
  )
}
