import React, { useState } from 'react'
import { DragDropContainer } from 'react-drag-drop-container'
import { itemProps } from '../interfaces/stateInterfaces'
import { getTheme } from '../theme'
import { Paper } from '@mui/material'

export const ItemFrom: React.FC<itemProps> = ({
  text,
  index,
  store,
  dispatch
}) => {
  const currentTheme = getTheme(store.themeIndex);
  return (
    <DragDropContainer targetKey={index}>
      <Paper
        onClick={() => {
          dispatch({
            type: 'RESET',
            value: null
          })

          dispatch({
            type: 'SELECT_FROM',
            value: index
          })
        }}
        sx={{
          position: 'relative',
          textAlign: 'center',
          transition: 'background-color .8s ease-out',
          color: currentTheme.palette.text.primary,
          borderRadius: '2em',
          borderStyle: 'solid',
          padding: '1em',
          width: '40vw',
          display: 'block',
          marginLeft: 'auto',
          marginRight: 'auto',
          marginTop: '1em',
          ':hover': {
            backgroundColor: currentTheme.palette.background.default,
            transition: 'background-color .1s ease-in'
          },
          userSelect: 'none',
          backgroundColor:
            store.selectedFrom === index
              ? currentTheme.palette.background.default
              : currentTheme.palette.background.paper
        }}
      >
        {text}
      </Paper>
    </DragDropContainer>
  )
}

export default ItemFrom
