import React from 'react'
import { keyframes, styled } from '@mui/material/styles'
import { Paper } from '@mui/material'
import { DragDropContainer } from 'react-drag-drop-container'
import { itemProps } from '../interfaces/stateInterfaces'
import { getTheme } from '../theme'

const shake = keyframes`
0% {
  width: 85%
},
50% {
  width: 84%
},
100% {
  width: 85%
}`

export const ItemTo: React.FC<itemProps> = ({
  text,
  index,
  store,
  dispatch
}) => {
  const theme = getTheme(store.themeIndex)
  const fnOnClick = () => {
    if (store.selectedFrom === null || store.selectedFrom === index) {
      dispatch({
        type: 'RESET',
        value: null
      })
    } else {
      dispatch({
        type: 'SELECT_TO',
        value: true,
        index
      })
    }
  }
  return (
    <Paper
      onClick={fnOnClick}
      sx={{
        animation: store.selectedTo[index]
          ? `${shake} 100ms infinite ease`
          : ``,
        backgroundColor: store.selectedTo[index]
          ? theme.palette.background.default
          : theme.palette.background.paper,
        position: 'relative',
        textAlign: 'center',
        transition: 'background-color .8s ease-out',
        color: theme.palette.text.primary,
        borderRadius: '2em',
        padding: '1em',
        width: '40vw',
        display: 'block',
        marginLeft: 'auto',
        borderStyle: 'solid',
        marginRight: 'auto',
        marginTop: '1em',
        ':hover': {
          backgroundColor: theme.palette.background.default,
          transition: 'background-color .1s ease-in',
          animation: `${shake} 100ms infinite ease`
        }
      }}
    >
      {text}
    </Paper>
  )
}

export default ItemTo
