import Box from '@mui/material/Box'
import * as React from 'react'
import { baseProps } from './interfaces/stateInterfaces'
import { Notice } from './components/Notice'

export const Home: React.FC<baseProps> = ({ store, dispatch }) => {
  const ref = React.useRef<HTMLDivElement>(null)
  return (
    <Box sx={{ pb: 7, width:'100%' }} ref={ref} alignItems={'center'} >
      <Notice store={store} dispatch={dispatch} />
    </Box>
  )
}
