import { AppBar, Icon, IconButton, Toolbar, Typography } from '@mui/material/'
import * as React from 'react'
import { baseProps } from '../interfaces/stateInterfaces'

export const Header: React.FC<baseProps> = ({ store, dispatch }) => {
  const fnOnClick = () => {
    dispatch({ type: 'SHOW_SIDEBAR', value: !store.sidebarActive, index: null })
  }
  return (
    <AppBar position="static">
      <Toolbar>
        <Typography variant="h5" width="100%">
          &nbsp;
          {store.pageIndex === 0 && <span>Home</span>}
          {store.pageIndex === 1 && <span>Mess</span>}
          {store.pageIndex === 2 && <span>Cards</span>}
          {store.pageIndex === 3 && <span>Profile</span>}
        </Typography>
        <IconButton aria-label="Menu">
          <Icon onClick={fnOnClick} fontSize="large">
            blur_on
          </Icon>
        </IconButton>
      </Toolbar>
    </AppBar>
  )
}
