import { Grid } from '@mui/material'
import Box from '@mui/material/Box'
import * as React from 'react'
import { ItemFrom } from './components/ItemFrom'
import { ItemTo } from './components/ItemTo'
import { baseProps } from './interfaces/stateInterfaces'

export const Mess: React.FC<baseProps> = ({ store, dispatch }) => {
  const ref = React.useRef<HTMLDivElement>(null)
  return (
    <Box sx={{ pb: 7 }} ref={ref}>
      <Grid
        container
        sx={{
          top: '4.5em',
          width: '100%',
          position: 'absolute',
          bottom: '4em',
          left: 0,
          textAlign: 'center'
        }}
      >
        <Grid item xs={6} sx={{ borderRight: 1, textAlign: 'center' }}>
          <ItemFrom
            key="1001"
            text="XXX"
            index={0}
            store={store}
            dispatch={dispatch}
          />
          <ItemFrom
            key="1002"
            text="YYY"
            index={2}
            store={store}
            dispatch={dispatch}
          />
        </Grid>

        <Grid
          item
          xs={6}
          sx={{
            borderLeft: 1,
            textAlign: 'center'
          }}
        >
          <ItemTo
            key="001"
            text="AAA"
            index={0}
            store={store}
            dispatch={dispatch}
          />
          <ItemTo
            key="002"
            text="BBB"
            index={1}
            store={store}
            dispatch={dispatch}
          />
          <ItemTo
            key="003"
            text="CCC"
            index={2}
            store={store}
            dispatch={dispatch}
          />
          <ItemTo
            key="004"
            text="DDD"
            index={3}
            store={store}
            dispatch={dispatch}
          />
        </Grid>
      </Grid>      
    </Box>
  )
}
